@import "~react-quill/dist/quill.snow.css";
@import "~perfect-scrollbar/css/perfect-scrollbar.css";
@import "~swiper/dist/css/swiper.min.css";
#root,
body,
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

html {
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.5;
  font-family: Roboto, "Helvetica Neue", sans-serif;
}

div,
a {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
.card-title {
  color: var(--text-body) !important;
  font-weight: 500;
}

.layout1,
.layout2,
.MuiPaper-root,
.MuiTableCell-body,
.matx-customizer {
  color: var(--text-body) !important;
}

